// BASIC
import React, { Component } from 'react'
import styled from 'styled-components'
import firebase, {db, au} from '../../../Config/firebase'
import {FaTimes, FaPlus, FaCheck, FaUserAlt, FaUserAltSlash} from 'react-icons/fa'
import {Prompt} from 'react-router-dom'

const AboutComponent = styled.div`
	width: 100%;
	max-width: 1000px;
	min-height: calc(100vh - 421.4px);
	display: flex;
	flex-direction: column;
	align-items: center;
	margin: 0 auto;
	padding: 20px;
	text-align: center;
`
const Delete = styled(FaTimes)`
	position: absolute;
	top: -10px;
	right: -10px;
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 25px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
`
const Add = styled.div`
	position: absolute;
	bottom: -10px;
	left: 50%;
	transform: translateX(-50%);
	color: var(--color-main);
	opacity: 0;
	transition: all 0.2s ease;
	font-size: 18px;
	padding: 3px;
	background-color: var(--color-bg);
	border-radius: 100%;
	@media (hover: hover) {
		:hover {
			cursor: pointer;
		}
	}
`
const Input = styled.input`
	width: 100%;
	margin: 10px 0;
	padding: 10px;
	font-size: 30px;
	font-weight: bold;
	border: 2px solid var(--color-opacity);
	text-align: center;
	:focus {
		border: 2px solid var(--color-main);
	}
`
const Textarea = styled.textarea`
	width: 100%;
	margin: 0;
	padding: 10px;
	font-size: 18px;
	height: 200px;
	border: 2px solid var(--color-opacity);
	resize: none;
	text-align: center;
	:focus {
		border: 2px solid var(--color-main);
	}
`
const Image = styled.div`
	position: relative;
	width: 90vw;
	max-width: 1000px;
	height: 300px;
	margin: 0 0 30px 0;
	background: url("${props => props.src}") no-repeat center center;
	background-size: cover;
	@media (min-width: 540px) {
		height: 400px;
		margin: 10px 0 30px 0;
	}
	:hover ${Delete}, :hover ${Add} {
		opacity: 1;
	}
`
const Title = styled.h1`
	width: 100%;
	position: relative;
	display: block;
	margin: 10px 0;
	font-size: 30px;
	outline: none;
	:hover ${Delete}, :hover ${Add} {
		opacity: 1;
	}
`
const Letter = styled.span`
	color: var(--color-main);
`
const Paragraph = styled.p`
	position: relative;
	width: 100%;
	margin: 20px 0;
	text-align: center;
	outline: none;
	:hover ${Delete}, :hover ${Add} {
		opacity: 1;
	}
`
const BrowserLink = styled.a`
	color: var(--color-main);
	font-weight: bold;
	text-decoration: none;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			opacity: 0.7;
		}
	}
`
const Save = styled(FaCheck)`
	font-size: 25px;
	color: var(--color-main);
	margin-top: 30px;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			transform: scale(1.1);
		}
	}
`
const View = styled.div`
	position: fixed;
	bottom: 30px;
	left: 30px;
	width: 50px;
	height: 50px;
	display: flex;
	justify-content: center;
	align-items: center;
	background-color: var(--color-bg);
	color: var(--color-main);
	border-radius: 100%;
	font-size: 20px;
	@media (hover: hover) {
		transition: all 0.2s ease;
		:hover {
			cursor: pointer;
			opacity: 0.7;
		}
	}
`
class About extends Component {
	state = {
		isChange: false,
		content: null,
		newContent: null,
		isAdmin: false,
		adminView: false
	}
	componentDidMount() {
		au.onAuthStateChanged(user => {
			if (user) {
				user.getIdTokenResult().then(idTokenResult => {
					if (idTokenResult.claims.admin) {
						this.setState({isAdmin: true, adminView: true})
					}
				})
			}
			else {
				this.setState({
					isAdmin: false
				})
			}
		})
		db.collection('pages').doc('about').get().then(snap => {
			this.setState({
				content: snap.data().content,
				newContent: snap.data().content
			})
		})
	}
	componentDidUpdate() {
		document.querySelectorAll('.editable').forEach(element => {
			element.addEventListener('paste', e => {
				e.preventDefault();
				const text = (e.originalEvent || e).clipboardData.getData('text/plain');
				document.execCommand("insertHTML", false, text);
			})
		})
	}
	sortById = (array) => {
		function compareNumbers(a, b) {
			if (a.id < b.id)
				return -1
			else if (a.id > b.id)
				return 1
			else {
				return 0
			}
		}
		return array.sort(compareNumbers)
	}
	delete = (num) => {
		if (!this.state.isChange) {
			this.setState({isChange: true})
		}
		const newContent =  this.state.newContent
		delete newContent[`element_${(num + 1).toString().padStart(2, '0')}`]
		this.setState({newContent, content: newContent})
	}
	add = () => {
		if (!this.state.isChange) {
			this.setState({isChange: true})
		}
		const nextElement = `element_${(Object.values(this.state.newContent).length + 1).toString().padStart(2, '0')}`
		this.setState(prevState => ({
			newContent: {
				...prevState.newContent,
				[nextElement]: {
					type: 'paragraph',
					text: '',
					id: Object.values(prevState.newContent).length + 1
				}
			}
		}), () => this.setState({content: this.state.newContent}));
	}
	addChanges = (e, element) => {
		// console.log(element)
		if (!this.state.isChange) {
			this.setState({isChange: true})
		}
		// e.currentTarget.innerHTML.replace(/<a.+<\/a>/g, x => {
		// 	link = x.replace('<a href="', '').replace(/".+/, '')
		// 	text = x.replace(/<a.+">/, '').replace('</a>', '')
		// });
		e.persist()
		this.setState(prevState => ({
			newContent: {
				...prevState.newContent,
				[element]: {
					...prevState.newContent[element],
					text: e.target.value
				}
			}
		}));
	}
	save = () => {
		if (this.state.isChange) {
			this.setState({isChange: false})
		}
		if (this.state.newContent) {
			// const content = {}
			// this.state.content.forEach((element, index) => {
			// 	content[index.toString()] = element
			// })
			db.collection('pages').doc('about').update({
				content: this.state.newContent
			})
		}
	}
	changeView = () => {
		this.setState(prevState => ({
			content: prevState.newContent,
			adminView: !prevState.adminView
		}));
	}
	render() {
		const contents = this.sortById(Object.values(this.state.content || {}))
		return (
			<AboutComponent>
				{!this.state.content ? '...' : contents.map(({type, text, src, href}, index) => {
					if (type === 'image') {
						return <Image src={src} id={index + 1} key={index}>
							{this.state.adminView && <div>
								<Delete onClick={() => this.delete(index)} />
								{index + 1 === contents.length && <Add onClick={() => this.add(index)}>
									<FaPlus />
								</Add>}
							</div>}
						</Image>
					}
					else if (type === 'title') {
						return <Title id={index + 1} key={index}>
							{this.state.adminView && <div>
								<Delete onClick={() => this.delete(index)} />
								{index + 1 === contents.length && <Add onClick={() => this.add(index)}>
									<FaPlus />
								</Add>}
							</div>}
							{this.state.adminView ? (
								<Input defaultValue={text} onChange={(e) => this.addChanges(e, `element_${(index + 1).toString().padStart(2, '0')}`)} />
							) : (
								this.state.adminView ? text : text.split('').map((letter) => /^[A-Z]*$/.test(letter) ? <Letter>{letter}</Letter> : letter)
							)}
						</Title>
					}
					else if (type === 'paragraph') {
						return <Paragraph id={index + 1} key={index}>
							{this.state.adminView && <div>
								<Delete onClick={() => this.delete(index)} />
								{index + 1 === contents.length && <Add onClick={() => this.add(index)}>
									<FaPlus />
								</Add>}
							</div>}
							{this.state.adminView ? (
								<Textarea onChange={(e) => this.addChanges(e, `element_${(index + 1).toString().padStart(2, '0')}`)}>{text}</Textarea>
							) : (
								text.split('--').map((fragment) => {
									fragment = fragment.replace(';)', '😉').replace(':)', '😃').replace(':D', '😁')
									if (fragment.includes('http')) {
										const text = fragment.replace(/\(.+\)/, '')
										const link = fragment.replace(/.+\(/, '').replace(')', '')
										return <BrowserLink href={link} target="_blank">{text}</BrowserLink>
									}
									else {
										return fragment
									}
								})
							)}
							{/* <div contentEditable={this.state.adminView}  suppressContentEditableWarning={true} className="editable">
								
							</div> */}
						</Paragraph>
					}
				})}
				{this.state.adminView && this.state.isChange && <Save onClick={this.save} />}
				{this.state.isAdmin && <>
					{this.state.adminView ? <View onClick={this.changeView} title="Zmień na widok użytkownika"><FaUserAlt /></View> : <View onClick={this.changeView} title="Zmień na widok administratora"><FaUserAltSlash /></View>}
				</>}
				<Prompt
					when={this.state.isChange}
					message='Zmiany nie zostały zapisane! Na pewno chcesz opuścić stronę?'
				/>
			</AboutComponent>
		);
	}
}

export default About